	module.exports = {"data":{"markdownRemark":{"html":"<h1>准备工作</h1>\n<h2>Web</h2>\n<ol>\n<li>域名</li>\n</ol>\n<p>现在域名不贵，建议去正规点的服务商购买，如阿里万网，新网，godaddy 等</p>\n<ol start=\"2\">\n<li>服务器</li>\n</ol>\n<p>常见的有各种 pages、博客托管、虚拟主机、云主机等方式，这里使用云主机。</p>\n<p>服务器放在国内，或者投放国内平台的广告的话，需要备案，推荐购买大一些的服务商提供的主机。放在国外可以免去备案的麻烦，但是速度肯定是要慢一些，可以购买香港、日本、美国主机，推荐的服务商有 linode、digitalocean、vultr 等，不要在上面放翻墙程序，以免 IP 被封。</p>\n<ol start=\"3\">\n<li>还没想到</li>\n</ol>","frontmatter":{"date":"December 06, 2018","path":"/build-a-web-blog-part-1/","title":"build a web blog part 1"}},"site":{"siteMetadata":{"siteName":"黑貓博客"}}},"pathContext":{}}

